.dice {
  display: flex;
}

.dice > button {
  border: none;
  background-color: transparent;
}

.dice > .roll {
  display: flex;
}

.dice > .roll > * {
  padding: 1em;
}

.dice > .remove {
  color: white;
  background-color: #0000004d;
}

.dice-4 {
  background-color: pink;
}

.dice-6 {
  background-color: blue;
}

.dice-10 {
  background-color: yellow;
}

.dice-20 {
  background-color: green;
}
